import * as React from "react"
import { Link, graphql } from "gatsby"
import { MDXProvider } from "@mdx-js/react"
import { unified } from "unified"
import remarkParse from "remark-parse"
import remarkMdx from "remark-mdx"
import remarkReact from "remark-react"
import remarkRehype from "remark-rehype"
import rehypeReact from "rehype-react"
import rehypeStringify from "rehype-stringify"
// import { MDXRenderer } from "gatsby-plugin-mdx"

import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"
import Comments from "../components/comments"
import Qna from "../components/qna"

const BlogPostTemplate = ({
  data: { previous, next, site, mdx: post },
  location,
}) => {
  const siteTitle = site.siteMetadata?.title || `Title`
  console.log(post.body)

  const components = {
    Test: props => (
      <h1 className="test" style={{ color: "red" }}>
        {props.children}
      </h1>
    ),
    MyComponent: () => <div>My Component</div>,
    "my-component": () => <div>here</div>,
  }
  // const processor = unified()
  //   .use(remarkParse)
  //   .use(remarkMdx)
  //   .use(remarkReact, {
  //     createElement: React.createElement,
  //     remarkReactComponents: {
  //       Test: props => <p style={{ color: "blue" }}>{props.children}</p>,
  //       p: props => <p style={{ color: "black" }}>{props.children}</p>,
  //     },
  //   })

  //   const jsx = processor.processSync(`
  //   # gpt .

  //   <Test></Test>
  //   <h1></h1>
  //   <p>test</p>

  //   ## gp
  // `).result

  //   const file = processor.parse(`
  //   # gpt .

  //   <Test></Test>
  //   <h1></h1>
  //   <p>test</p>

  //   ## gp
  // `)
  //   console.log("AST:", file)

  //   processor.run(file, function (error, node, parent) {
  //     console.log("Node:", node)
  //   })

  //   const jsx2 = processor.stringify(file)
  //   console.log("Result:", jsx2)

  //   // console.log(jsx)

  const processor = unified()
    // Markdown  MDX 
    .use(remarkParse)
    .use(remarkMdx)
    // Markdown  MDX HAST 
    .use(remarkRehype)
    // HAST React  
    .use(rehypeReact, {
      // createElement: React.createElement,
      createElement: React.createElement,
      components,
    })

  const mdx = `
# Hello, world!

This is an **MDX** document.

<MyComponent />

- Item 1
- Item 2
- Item 3
`

  const hast = processor.parse(mdx)
  const html = processor.stringify(hast)

  console.log("hast:", hast)
  console.log("html:", html)

  const jsx = processor.processSync(mdx).result

  console.log("mdx:", mdx)
  console.log("jsx:", jsx)

  return (
    <Layout location={location} title={siteTitle}>
      <article
        className="blog-post"
        itemScope
        itemType="http://schema.org/Article"
      >
        <header>
          <h1 itemProp="headline">{post.frontmatter.title}</h1>
          <p>{post.frontmatter.date}</p>
        </header>
        <MDXProvider components={components}>{jsx}</MDXProvider>

        {/* <div
          dangerouslySetInnerHTML={{ __html: post.body }}
          itemProp="articleBody"
        /> */}

        <hr />
        <footer>
          <Bio />
        </footer>
      </article>

      <Comments></Comments>
    </Layout>
  )
}

export const Head = ({ data: { mdx: post } }) => {
  return (
    <Seo
      title={post.frontmatter.title}
      description={post.frontmatter.description || post.excerpt}
    />
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    mdx(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      body
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
      }
    }
    previous: mdx(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: mdx(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`
