import * as React from "react"
import styled from "@emotion/styled"

function Qna({ texts }) {
  return (
    <div>
      <div>test</div>
      {texts.map((text, idx) => {
        if (idx % 2 === 0) {
          return <Question key={`answer-${idx}`}>{text}</Question>
        } else {
          return <Answer key={`question-${idx}`}>{text}</Answer>
        }
      })}
    </div>
  )
}

export default Qna

const Question = styled.div``

const Answer = styled.div``
